.Message {
    border: 1px solid #EFEEEE;
    border-radius: 5px;
    padding: 20px;
    background-color: #FFF;
    width: 80vw;
    margin-bottom: 40px;
}

/*TODO: media width под мобилы */

.MessageText {
    line-height: 40px;
    letter-spacing: 1px;
    font-size: 16px;
}

@media screen and (min-width: 900px) {
    .Message {
        width: 50vw;
    }
}